*{
    font-family: "Montserrart", sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

ul{
    list-style: none;
}

a{
    text-decoration: none;
}

h2{
    font-weight: 500;
    font-size: 36px;
    color: #283d50;
    margin-bottom: 60px;
}

h3{
    color: #444;
    font-size: 24px;
    margin-bottom: 24px;
}

h4{
    font-size: 20px;
    margin-bottom: 8px;
    color: #283d50;
}

//nav

header{
    height: 110px;
    position: fixed;
    width: 100%;
    z-index: 3;
    background-color: #fff;
    box-shadow: 0px 0px 30px rgba(39, 60, 145, 0.897);
    
    nav{
        display: flex;
        align-items: center;
        padding: 25px 10px;
    
        img{
            height: 80px; //logonun büyüklüğü
        }
        ul{
            flex: 1;
            justify-content: flex-end;
            display: none;
            @media (min-width:768px) {
                display: flex;
            }
            a{
                color: #004289;
                font-size: 14px;
                font-weight: 600;
                padding: 0 15px;
                margin-left: 15px;
                
            }    
        }
    }   
}
//hero

.hero{
  padding: 150px 0;
  background: url("/img/intro-bg.png") center bottom no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;

  .flex-row{
    padding: 0 25px;
    gap: 150px;
    flex-direction: column;
    align-items: center;
    @media(min-width: 1000px){
        flex-direction: row;
    }
  }
  h2{
    font-size: 40px;
    color: #fff;
    text-align: center;
    margin-bottom: 32px;
    @media (min-width: 1000px) {
        text-align: initial;
        font-size: 34px;
    }   
    }

    .left{ //ekranı küçültünce veya mobil görünümd resmi ortayı almak için
        flex: 1;
        order: 2;
        justify-content: center;
        align-items: center;
        @media(min-width: 1000px){
            order: 1;
        }
    }
    .buttons{
        display: flex;
        flex-direction: column;
        align-items: center;
        @media(min-width: 1000px){
            flex-direction: row;
        }
        .btn{
            text-align: center;
            width: 200px;
            margin-bottom: 16px;
            @media(min-width: 1000px){
                margin-right: 16px;
            }
        }
    }

    img{
        display: flex;
        flex:1;
        order: 1;
        width: 50%;
        @media(min-width: 1000px){
            order: 2;
        }
    }
  }


//utility classes
.container{
    max-width: 1140px;
    margin: 0 auto;
    padding: 15px 20px; //logoyu mavi alanda ortalamak için
}
.flex-row{
    display:flex;
}

.btn{
    cursor: pointer;
    display: inline-block;
    padding: 14px 40px;
    background-color: #007bff;
    color: #fff;
    border-radius: 45px;
    border: 2px solid transparent;
    font-weight: 600;
    transition: 500ms ease all;

    &:hover{
        background-color: transparent;
        color: #fff;
    }
}
.btn-light{
    background-color: transparent;
    border-color: #fff;

    &:hover{
        background-color: #007bff;
        border-color: transparent;
    }
}